import { Person } from './lib/classes';

import {
    KmsKeyringBrowser,
    KMS,
    getClient,
    buildClient,
    CommitmentPolicy,
  } from '@aws-crypto/client-browser'
  import { toBase64 } from '@aws-sdk/util-base64-browser'


// create a `Person` instance
const person: Person = new Person( 'John', 'Doe' );


console.log( person.getFullName() );


//do your crypto stuff

//declare const credentials: {accessKeyId: string, secretAccessKey:string, sessionToken:string }

const { encrypt, decrypt } = buildClient(
    CommitmentPolicy.REQUIRE_ENCRYPT_REQUIRE_DECRYPT
  )

const accessKeyId = 'AKIAWHAWQR6QLERVWGNX';
const secretAccessKey = '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';

const clientProvider = getClient(KMS, {
  credentials: {
    accessKeyId,
    secretAccessKey
  }
});

const generatorKeyId = 'arn:aws:kms:us-east-1:427396468640:key/27a5f549-b997-4497-8ce6-102fee110fbb'
//const keyIds = ['arn:aws:kms:us-east-1:427396468640:key/34b87c54-d79d-4631-97bb-7abea0c6a24c']
//const keyring = new KmsKeyringBrowser({ clientProvider, generatorKeyId, keyIds })

const keyring = new KmsKeyringBrowser({ clientProvider, generatorKeyId })

const context = {
    stage: 'demo',
    purpose: 'simple demonstration app',
    origin: 'us-west-1'
  }


const EXAMPLE_DATA = new Uint8Array([1, 2, 3, 4, 5])

const { result }  =  await encrypt(keyring, EXAMPLE_DATA, { encryptionContext: context });

const resultBase64 = toBase64(result);

console.log(resultBase64)

const { plaintext, messageHeader } = await decrypt(keyring, result)

const { encryptionContext } = messageHeader

